@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
* {
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto Slab', serif;
}

.app {
	display: flex;
	flex-direction: column;
	height: 100vh;
	overflow: hidden;
}

.vending-nav {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	height: 5%;
	background-color: salmon;
}

.vending-nav a {
	text-decoration: none;
	color: white;
}

.vending-nav a:hover {
	cursor: pointer;
}

.vending-nav .nav-active {
	color: black;
}

.contenido {
	height: 95%;
	position: relative;
	overflow: hidden;
}

.vending-machine {
	background: url(/vendingmachine/static/media/vending.35e3b785.jpg) no-repeat center center fixed;
	background-size: 45%;
	height: 95vh;
	display: flex;
	flex-direction: column;
}

.vending-welcome {
	display: flex;
	height: 90%;
	width: 90%;
	align-self: center;
	justify-content: space-between;
	align-items: center;
	text-align: center;
}

.vending-welcome .vending-mensaje {
	width: 35%;
}

.vending-machine .vending-menu {
	width: 35%;
}

.vending-machine .vending-menu h3 {
	margin-bottom: 2px;
	text-decoration: underline;
}

.vending-machine .vending-menu ul {
	list-style: none;
	font-weight: bold;
	text-align: center;
	padding: 0;
}

.vending-machine .vending-menu a {
	text-decoration: none;
	color: black;
}

.vending-machine .vending-menu a:hover {
	cursor: pointer;
	color: salmon;
}

.saladito {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	overflow: hidden;
}

.saladito .saladito-menu {
	display: flex;
	flex-direction: column;
	height: 20%;
	width: 20%;
	background-color: rgba(255, 255, 255, 0.418);
	border-radius: 15px;
	justify-content: center;
	align-items: center;
}

.saladito .saladito-menu button {
	border: none;
	border-radius: 10px;
	padding: 15px 20px;
	margin: 5px;
	font-family: inherit;
	-webkit-transition: all 0.2s ease-in;
	transition: all 0.2s ease-in;
	font-weight: bold;
}

.saladito .saladito-menu button:hover {
	-webkit-transform: translateY(-2px);
	        transform: translateY(-2px);
	cursor: pointer;
}

.saladito-btn {
	background-color: salmon;
}

.saladito-volver {
	background-color: black;
	color: white;
}

.saladito-volver a {
	text-decoration: none;
	color: white;
}

@-webkit-keyframes girar {
	0% {
		-webkit-transform: rotate(0);
		transform: rotate(0);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@keyframes girar {
	0% {
		-webkit-transform: rotate(0);
		transform: rotate(0);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@-webkit-keyframes aparecer {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes aparecer {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.coca {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.coca-menu {
	display: flex;
	align-items: center;
	justify-content: space-between;
	text-align: center;
	width: 100%;
	height: 40%;
}

.coca-form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.coca-menu img {
	width: 100%;
}

.coca-form label {
	font-family: inherit;
	font-size: 1.1rem;
}

.coca-form input {
	font-family: inherit;
	font-size: 1.1rem;
}

.coca-form button {
	border: none;
	background-color: salmon;
	color: white;
	padding: 10px 15px;
	font-size: 1.1rem;
	margin: 10px;
	border-radius: 5px;
	-webkit-transition: all 0.2s ease-in;
	transition: all 0.2s ease-in;
}

.coca-form button:hover {
	-webkit-transform: translateY(-2px);
	        transform: translateY(-2px);
	cursor: pointer;
}

.precio {
	font-size: 3rem;
	color: darkgreen;
	font-family: inherit;
}

